import { useRecoilValue } from "recoil";
import { loggedMemberAtom } from "../atoms";

function MemberInfo() {
  const loggedMember = useRecoilValue(loggedMemberAtom);

  const birth = new Date(loggedMember.birth);
  const birthStr =
    birth.getFullYear() +
    " " +
    birth.getMonth() +
    " " +
    birth.getDay() +
    "";

  return (
    <div>
      <div>. {loggedMember.id} </div>
      <div>: {loggedMember.name}</div>
      <div>: {loggedMember.isMale ? "" : ""}</div>
      <div>: {loggedMember.address}</div>
      <div>: {birthStr}</div>
      <div>: {loggedMember.email}</div>
      <div>: {loggedMember.phoneNumber}</div>
    </div>
  );
}

export default MemberInfo;
